$circle-size: 50px;
$circle-background: transparent;
$circle-color: #d9ecf3;
$inset-size: 38px;
$inset-color: #d9ecf3;
$transition-length: 1s;

.progress {
	&__nmb {
		font-size: 10px;
		display: inline-block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
		color: #54c4ef; } }

.ko-progress-circle {
	position: absolute;
	top: 50%;
	left: 50%;
	width: $circle-size;
	height: $circle-size;
	transform: translate(-50%, -50%) rotate(180deg);
	background-color: $circle-background;
	border-radius: 50%;
	.ko-progress-circle__slice, .ko-progress-circle__fill {
		width: $circle-size;
		height: $circle-size;
		position: absolute;
		-webkit-backface-visibility: hidden;
		// transition: transform $transition-length;
		border-radius: 50%; }
	.ko-progress-circle__slice {
		clip: rect(0px, $circle-size, $circle-size, $circle-size / 2);
		.ko-progress-circle__fill {
			clip: rect(0px, $circle-size / 2, $circle-size, 0px);
			background-color: $circle-color; } }
	.ko-progress-circle__overlay {
		width: $inset-size;
		height: $inset-size;
		position: absolute;
		margin-left: ($circle-size - $inset-size) / 2;
		margin-top: ($circle-size - $inset-size) / 2;
		background-color: $inset-color;
		border-radius: 50%; }
	$i: 0;
	$increment: 180deg / 100;
	@while $i <= 100 {
		&[data-progress='#{$i}'] {
			.ko-progress-circle__slice.full, .ko-progress-circle__fill {
				transform: rotate(-$increment * $i); }
			.ko-progress-circle__fill.ko-progress-circle__bar {
				transform: rotate(-$increment * $i * 2); }
			$i: $i + 1; } } }

.descr {
