	font-size: 18px;
	letter-spacing: .03em;
	color: $c-text;
	padding: 35px 55px;
	position: relative;
	border: 3px solid  #d9ebf5;
	border-image: url('../img/border2.png') 3;
	p {
		&:not(:last-child) {
			margin-bottom: 18px; } }
	@include xl {
		font-size: 16px; }
	@include lg {
		padding: 25px 35px; }
	@include md {
		padding: 20px 25px;
		br {
			display: none; } }
	@include sm {
		font-size: 14px;
		padding: 20px; }
	&__wrap {
		position: relative;
		padding-left: 100px;
		padding-right: 100px;
		@include cont(1442);
		@include lg {
			padding-left: 70px;
			padding-right: 70px; }
		@include xs {
			padding-left: 0;
			padding-right: 0; } }
	&__ind {
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		display: inline-block;
		font-size: 0;
		@include xs {
			display: none; }
		&.is-left {
			left: 0; }
		&.is-right {
