	&__top-left,
	&__top-right,
	&__bottom-left,
	&__bottom-right {
		display: inline-block;
		width: 37px;
		height: 37px;
		position: absolute;
		.ic {
			@include vcenter;
			transition: visibility .5s, opacity .5s; }
		.ic-corner-a {
			visibility: hidden;
			opacity: 0; }
		&.is-active {
