				visibility: visible;
				opacity: 1; } } }
	&__top-left {
		top: -13px;
		left: -11px; }
	&__top-right {
		@include rotate(90);
		top: -13px;
		right: -11px; }
	&__bottom-left {
		@include rotate(-90);
		bottom: -13px;
		left: -11px; }
	&__bottom-right {
		@include rotate(180);
		bottom: -13px;
		right: -11px; } }

