a {
	text-decoration: none;
	color: inherit; }

i {
	font-style: normal; }

strong {
	font-weight: inherit; }

small {
	font-size: inherit; }

body {
	@include image('bg.jpg');
	@include cover;
	background-color: #a5c6d9;
	line-height: 1.3;
	font-size: 16px;
	color: $c-gray;
	@include MRegular;
	@include no-scroll; }

.out {
	overflow: hidden; }

.logo {
	display: inline-block;
	font-size: 0;
	img {
		max-width: 100%; } }

h1, .h1 {
	font-size: 80px;
	color: $c-red;
	@include MBold;
	text-transform: uppercase;
	line-height: 1.2;
	i {
		text-transform: none; }
	@include lg {
		font-size: 70px; }
	@include md {
