h2, .h2 {
	font-size: 50px;
	@include MBold;
	color: $c-text;
	text-transform: uppercase;
	line-height: 1.2;
	@include lg {
		font-size: 42px; }
	@include md {
		font-size: 38px; }
	@include sm {
		font-size: 35px; }
	@include xs {
