h3, .h3 {
	font-size: 25px;
	@include MBold;
	color: $c-text;
	text-transform: uppercase;
	line-height: 1.5;
	@include lg {
		font-size: 20px; }

	&_comb {
		i {
			color: $c-red; } } }

.year {
	@include MThin;
	font-size: 180px;
	line-height: 1;
	color: #ddeef6;
	padding: 30px 70px 10px;
	display: inline-block;
	position: relative;

	&:hover .year__top-left {
		transform: translate(30px, 30px) rotate(90deg);
		@include md {
			transform: translate(20px, 20px) rotate(90deg); } }
	&:hover .year__top-right {
		transform: translate(-30px, 30px) rotate(180deg);
		@include md {
			transform: translate(-20px, 20px) rotate(180deg); } }
	&:hover .year__bottom-left {
		transform: translate(30px, -30px) rotate(0deg);
		@include md {
			transform: translate(20px, -20px) rotate(0deg); } }
	&:hover .year__bottom-right {
		transform: translate(-30px, -30px) rotate(-90deg);
		@include md {
			transform: translate(-20px, -20px) rotate(-90deg); }
		@include lg;
		font-size: 150px; }
	@include md {
		font-size: 130px;
		padding: 20px 50px 10px; }
	@include sm {
		font-size: 100px; }
	@include xs {
		font-size: 75px; }
	&__top-left,
	&__top-right,
	&__bottom-left,
	&__bottom-right {
		position: absolute;
		display: inline-block;
		transition: transform .2s;
		@include sm {
			width: 18px;
			height: 18px;
			border-width: 3px; } }
	&__top-left {
		@include icon-arrow(23, 23, 4, #ddeef6, 90);
		top: 0;
		left: 0; }
	&__top-right {
		@include icon-arrow(23, 23, 4, #ddeef6, 180);
		top: 0;
		right: 0; }
	&__bottom-left {
		@include icon-arrow(23, 23, 4, #ddeef6, 0);
		bottom: 0;
		left: 0; }
	&__bottom-right {
		@include icon-arrow(23, 23, 4, #ddeef6, -90);
		bottom: 0;
		right: 0; } }

.fadeIn {
	opacity: 0;
	transition-property: opacity;
	&.is-animated {
		opacity: 1;
		transition-duration: 3s; } }
.zoomIn {
	@include scale(0);
	opacity: 0;
	transition-property: opacity, transform;
	&.is-animated {
		@include scale(1);
		opacity: 1;
		transition-duration: 3s; } }
.top {
	background-color: rgba(#ddeef6, .6);
	border-radius: 10em;
	bottom: 80px;
	height: 50px;
	position: fixed;
	right: 30px;
	transition: transform 0.25s ease 0s;
	width: 50px;
	z-index: 10;
	text-align: center;
	transform: translate(200%, 0);
	transition: background-color .2s;
	@include hover {
		background-color: #ddeef6; }
	@include lg {
		bottom: 60px; }
	@include sm {
		right: 20px; }
	&::before {
		content: '';
		display: inline-block;
		@include icon-arrow(15, 15, 2, $c-text, 135);
		position: absolute;
		left: 50%;
		top: 1.3em;
		transform: translate(-50%, 0) rotate(135deg); }
	&.is-active {
		transform: translate(0, 0); } }
