@include font('Montserrat-Bold', 'Montserrat-Bold');
@include font('Montserrat-Light', 'Montserrat-Light');
@include font('Montserrat-Medium', 'Montserrat-Medium');
@include font('Montserrat-Regular', 'Montserrat-Regular');
@include font('Montserrat-Thin', 'Montserrat-Thin');
@mixin MBold {
	font-family: 'Montserrat-Bold' ,sans-serif; }
@mixin MLight {
	font-family: 'Montserrat-Light',sans-serif;
	font-weight: normal; }
@mixin MMedium {
	font-family: 'Montserrat-Medium',sans-serif; }
@mixin MRegular {
	font-family: 'Montserrat-Regular',sans-serif; }
@mixin MThin {
	font-family: 'Montserrat-Thin',sans-serif; }

