body {
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0%;
  font-size: 16px;
  color: #58595b;
  font-family: 'Montserrat-Regular',sans-serif;
}

.header {
  box-shadow: 2px 3px 31px rgba(0, 234, 255, 0.19);
  background-color: rgba(241, 250, 255, 0.6);
}

.header__inner {
  display: flex;
  min-height: 112px;
  align-items: center;
}

.header__logo {
  display: inline-flex;
}

.header__logos {
  max-width: 580px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.header__year {
  margin-left: auto;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.hero {
  text-align: center;
}

.hero__heading .title {
  margin-bottom: 30px;
}

.hero__heading .title--h1 {
  margin-bottom: 8px;
}

.hero__logo {
  padding-top: 28px;
  margin: 0 auto 41px;
  text-align: center;
  max-width: 567px;
  width: 100%;
  height: auto;
  position: relative;
  left: 9px;
}

.triangle {
  animation: MoveUpDown 1.5s linear infinite;
  display: inline-flex;
  position: relative;
  top: 0;
  right: 0;
  margin-bottom: 45px;
}

@keyframes MoveUpDown {
  0%, 100% {
    top: 0;
  }
  50% {
    top: 50px;
  }
}

.circle-progress {
  position: absolute;
  bottom: 50%;
  transform: translate(0%, 50%);
}

.circle-progress--left {
  left: 0;
}

.circle-progress--right {
  right: 0;
}

.circle-progress svg {
  font-size: 66px;
  fill: #53C4EF;
}

#object {
  transition: 4s rotate linear;
}

.hero__main {
  width: 200px;
  height: 70px;
  position: absolute;
  top: 100%;
  left: 50%;
  perspective: 800px;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.hero__btn {
  width: 100%;
  height: 100%;
  display: block;
  background-color: #3f0063;
  font-family: fantasy;
  font-size: 32px;
  color: #04c5D6;
  text-align: center;
  line-height: 70px;
  text-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0 15px 30px rgba(59, 2, 97, 0.4);
  transition: all .5s;
  cursor: pointer;
}

.hero__effect {
  width: 100%;
  height: 30px;
  background-color: #3b0261;
  position: absolute;
  top: 79%;
  left: 0;
  transform: rotateX(-87deg);
  transition: all .5s;
}

.hero__main:hover .hero__btn {
  color: #52dee7;
  background-color: #4a0472;
  transform: rotateX(25deg);
  text-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0 25px 30px rgba(59, 2, 97, 0.4);
}

.hero__main:hover .hero__effect {
  transform: rotateX(-70deg);
}

.progress__nmb {
  font-size: 10px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  color: #54c4ef;
}

.ko-progress-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%) rotate(180deg);
  background-color: transparent;
  border-radius: 50%;
}

.ko-progress-circle .ko-progress-circle__slice, .ko-progress-circle .ko-progress-circle__fill {
  width: 50px;
  height: 50px;
  position: absolute;
  -webkit-backface-visibility: hidden;
  border-radius: 50%;
}

.ko-progress-circle .ko-progress-circle__slice {
  clip: rect(0px, 50px, 50px, 25px);
}

.ko-progress-circle .ko-progress-circle__slice .ko-progress-circle__fill {
  clip: rect(0px, 25px, 50px, 0px);
  background-color: #d9ecf3;
}

.ko-progress-circle .ko-progress-circle__overlay {
  width: 38px;
  height: 38px;
  position: absolute;
  margin-left: 6px;
  margin-top: 6px;
  background-color: #d9ecf3;
  border-radius: 50%;
}

