.descr {
  text-align: center;
  font-size: 18px;
  letter-spacing: .03em;
  color: #839ca6;
  padding: 35px 55px;
  position: relative;
  border: 3px solid  #d9ebf5;
  border-image: url("../img/border2.png") 3;
}

.descr p:not(:last-child) {
  margin-bottom: 18px;
}

@media only screen and (max-width: 1559px) {
  .descr {
    font-size: 16px;
  }
}

@media only screen and (max-width: 1199px) {
  .descr {
    padding: 25px 35px;
  }
}

@media only screen and (max-width: 1023px) {
  .descr {
    padding: 20px 25px;
  }
  .descr br {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .descr {
    font-size: 14px;
    padding: 20px;
  }
}

.descr__wrap {
  position: relative;
  padding-left: 100px;
  padding-right: 100px;
  max-width: 1442px;
  margin: 0 auto;
}

@media only screen and (max-width: 1199px) {
  .descr__wrap {
    padding-left: 70px;
    padding-right: 70px;
  }
}

@media only screen and (max-width: 599px) {
  .descr__wrap {
    padding-left: 0;
    padding-right: 0;
  }
}

.descr__ind {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  display: inline-block;
  font-size: 0;
}

@media only screen and (max-width: 599px) {
  .descr__ind {
    display: none;
  }
}

.descr__ind.is-left {
  left: 0;
}

.descr__ind.is-right {
  right: 0;
}

.descr__top-left, .descr__top-right, .descr__bottom-left, .descr__bottom-right {
  display: inline-block;
  width: 37px;
  height: 37px;
  position: absolute;
}

.descr__top-left .ic, .descr__top-right .ic, .descr__bottom-left .ic, .descr__bottom-right .ic {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: visibility .5s, opacity .5s;
}

.descr__top-left .ic-corner-a, .descr__top-right .ic-corner-a, .descr__bottom-left .ic-corner-a, .descr__bottom-right .ic-corner-a {
  visibility: hidden;
  opacity: 0;
}

.descr__top-left.is-active .ic-corner-a, .descr__top-right.is-active .ic-corner-a, .descr__bottom-left.is-active .ic-corner-a, .descr__bottom-right.is-active .ic-corner-a {
  visibility: visible;
  opacity: 1;
}

.descr__top-left {
  top: -13px;
  left: -11px;
}

.descr__top-right {
  transform: rotate(90deg);
  top: -13px;
  right: -11px;
}

.descr__bottom-left {
  transform: rotate(-90deg);
  bottom: -13px;
  left: -11px;
}

.descr__bottom-right {
  transform: rotate(180deg);
  bottom: -13px;
  right: -11px;
}

.descr-section {
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 1023px) {
  .descr-section {
    padding-bottom: 30px;
  }
}

